.features {
    padding-top: 60px;
    padding-bottom: 60px;
    background-color: #f6f6f6;
}
.features .intro {
    text-align: center;
    font-size: 20px;
}
.features .container{
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    gap: 20px;
}
.features .container .feat{
    padding: 20px;
    text-align: center;
}
.features .container .feat img {
    width: 285px;
    height: 190px;
    border-radius: 1.2rem;
}
.features .container .feat h3{
    font-weight: 800;
    margin: 20px 0;
    color: black;
}
.features .container .feat p{
    color: #777;
    font-size: 17px;
    line-height: 1.2;
}
