.footer {
    padding: 40px 0;
    background: rgb(255,255,255);
    background: linear-gradient(90deg, rgba(255,255,255,1), rgba(168,161,150,1) 65%);
}
.footer-wrapper {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    padding: 0 4rem;
    text-align: center;
}

.footer h3 {
  font-size: 18px;
  margin-bottom: 10px;
}

.footer p {
  font-size: 14px;
  margin-bottom: 5px;
}

.footer-bottom {
  padding: 10px 0;
  text-align: center;
}

.footer-bottom p {
  font-size: 12px;
}

@media (max-width: 768px) {
  .footer-wrapper {
    display: flex;
    flex-direction: column;
    width: 100%;
    text-align: left;
  }
  .footer-content {
    width: inherit;
  }
  .footer-content p {
    font-size: 13px;
  }
  .footer-bottom {
    font-size: 8px;
  }
}