.navbar {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 2rem;
    font-family: var(--font-alt);
    background: rgb(255,255,255);
    background: linear-gradient(90deg, rgba(255,255,255,1) 15%, rgba(168,161,150,1) 75%);
}
.navbar .logo {
    display: flex;
    align-items: center;
    justify-content: flex-start;
}
.navbar .logo img {
    width: 150px;
}
.navbar .links {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    list-style: none;
}
.navbar .links li {
    margin: 0 1rem;
    cursor: pointer;
    color: #00043c;
}
.navbar .links li:hover {
    color: white;
}
.navbar .login {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}
.navbar .login .btn {
    width: 150px;
    height: 50px;
    color: white;
    background: inherit;
    font-family: var(--font-alt);
    cursor: pointer;
    position: relative;
    overflow: hidden;
    border: none;
}
.navbar .login .btn span {
    font-size: 15px;
    letter-spacing: 1px;
    transition: top 0.5s;
}

.navbar .login .btn-text-one {
    position: absolute;
    width: 100%;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
}

.navbar .login .btn-text-two {
    position: absolute;
    width: 100%;
    top: 150%;
    left: 0;
    transform: translateY(-50%);
}

.navbar .login .btn:hover .btn-text-one {
    top: -100%;
}

.navbar .login .btn:hover .btn-text-two {
    top: 50%;
}
.navbar .login div {
    width: 1px;
    height: 30px;
    background-color: aliceblue;
}
.navbar .smallScreen {
    display: none;
}
.navbar .smallScreen .overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    transition: 0.2 ease;
    flex-direction: column;
    background:#f7f7f7;
}
.navbar .smallScreen .overlay .close {
    font-size: 27px;
    cursor: pointer;
    position: absolute;
    top: 20px;
    right: 20px;
}
.navbar .smallScreen .open {
    cursor: pointer;
}
.navbar .smallScreen .linksSmall {
    list-style: none;
}
.navbar .smallScreen .linksSmall li {
    margin: 2rem;
    cursor: pointer;
    color: black;
    font-size: 2rem;
    text-align: center;
    font-family: var(--font-alt);
}
.navbar .smallScreen .linksSmall li:hover {
    color: blue;
}
.navbar .smallScreen .sign {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 15px;
}
.navbar .smallScreen .sign .btn {
    width: 150px;
    height: 50px;
    background: inherit;
    color: black;
    font-family: var(--font-alt);
    cursor: pointer;
    position: relative;
    overflow: hidden;
    border: none;
}

.navbar .smallScreen .sign .btn span {
    font-size: 15px;
    letter-spacing: 1px;
    transition: top 0.5s;
}

.navbar .smallScreen .sign .btn-text-one {
    position: absolute;
    width: 100%;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
}

.navbar .smallScreen .sign .btn-text-two {
    position: absolute;
    width: 100%;
    top: 150%;
    left: 0;
    transform: translateY(-50%);
}

.navbar .smallScreen .sign .btn:hover .btn-text-one {
    top: -100%;
}

.navbar .smallScreen .sign .btn:hover .btn-text-two {
    top: 50%;
}
.navbar .smallScreen .sign .stick {
    width: 3px;
    height: 30px;
    background-color: aliceblue;
}
@media (max-width: 1150px) {
    .navbar .links {
        display: none;
    }
    .navbar .smallScreen {
        display: flex;
    }
}
@media (max-width: 650px) {
    .navbar {
        background: rgb(255,255,255);
        background: linear-gradient(90deg, rgba(255,255,255,1) 33%, rgba(168,161,150,1) 100%);
}
    .navbar .links {
        padding: 1rem;
    }
    .navbar .login {
        display: none;
    }
    .navbar .smallScreen {
        display: flex;
    }
    .navbar .logo img {
        width: 110px;
    }
}
