.order-and-reservation-container {
    margin-top: 60px;
    margin-bottom: 60px ;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.order-section,
.reservation-section {
    flex: 1;
    margin:20px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    text-align: center;
}
h2, p, button {
    margin-bottom: 10px;
}
.order-button,
.reservation-button {
    padding: 10px 20px;
    background-color: #183153;
    color: #fff;
    border: none;
    border-radius: 10px;
    font-size: 16px;
    cursor: pointer;
    font-family: var(--font-alt)
}
.order-button:hover,
.reservation-button:hover {
    background-color: #C56824;
}
@media (max-width: 768px) {
.order-and-reservation-container {
    flex-direction: column;
}

.order-section,
.reservation-section {
    margin-right: 0;
    margin-bottom: 20px;
}
}