.header{
    background-image: linear-gradient(rgba(0,0,0,0.6), rgba(0,0,0,0.6)),url("../../assets/pexels-afta-putta-gunawan-683039.jpg");
    background-size: cover;
    background-position: center;
    height: calc(100vh - 182px );
    display: flex;
    justify-content: center;
    align-items: center;
}
.header .container {
    color: white;
    text-align: center;
}
.header .container h1 {
    margin-bottom: 1.2rem;
}
.header .container .icons {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 15px;
  margin-top: 1.2rem;
  font-size: 30px;
}
@media (max-width:650px) {
    .header {
        height: calc(100vh - 142px);
    }
    .header .container {
      width: 320px;
      max-width: 100%;
      margin: 2rem;
    }
    .header .container h1 {
      margin-bottom:20px;
      font-size: 29px;
      font-weight: bold;
    }
    .header .container p {
      font-size: 18px;
    }
}

