.menu-container {
  display: flex;
  justify-content: space-between;
  background-image: linear-gradient(rgba(0,0,0,0.6), rgba(0,0,0,0.6)),url("../../assets/menu.jpg");
  background-size: cover;
  background-position: center;
  color: white;
}

.menu-section {
  flex: 1;
  color: white;
  margin-top: 25px;
  margin-bottom: 25px;
  padding: 10px;
}
.menu-section h2 {
  text-align: center;
}
.menu-items {
  list-style-type: none;
  margin: 1rem;
}

.menu-item {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}

.item-name {
  font-weight: bold;
}

.item-price {
  margin-left: 10px;
}
@media (max-width:650px) {
    .menu-section {
    margin-top: 15px;
    margin-bottom: 15px;
    padding: 5px;
    }
}